.wrapper {
	width: 300px;
	padding: 20px;
	margin-left: 30px;
	background-color: rgba(0, 0, 0, 0.65);
	border-radius: 10px;
	color: #fff;
}

.list__container {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.list__item {
	padding: 5px;
	margin-bottom: 7px;
}

.item__title {
	text-decoration: underline;
	text-shadow: 0 0 2px #fff, 0 0 40px blue;
}

@media screen and (max-width: 700px) {
	.wrapper {
		width: 100%;
		margin: 0;
		margin-top: 10px;
	}
}
